<template>
  <div>

    <div>
      条件
      <select v-model="selected">
        <option v-for="item in option" :value="item.value" :key="item.value">{{ item.text }}</option>
      </select>
      <input type="text" v-model="like"> <button @click="doLike">确定</button>
    </div>

    <table>
      <tr>
        <th>id</th>
        <th>name</th>
        <th>price</th>
        <th>sold</th>
        <th>img</th>
      </tr>
      <tr v-for="item in list" :key="item.id">
        <td>{{item.id}}</td>
        <td><router-link :to="{path: `/get/goodsDetail/${item.id}`}">{{item.name}}</router-link></td>
        <td>{{item.price |price}}</td>
        <td>{{item.sold |sold}}</td>
        <td><img :src="doImg(item.img)" class="img"></td>
      </tr>
    </table>

  </div>
</template>

<script>
export default {
  data(){
    return{
      list: [],
      /*
      */
      like: '',
      selected: 0,
      option:[
        {text: '全部', value: 0},
        {text: '零食' , value: 1},
        {text: '书籍' , value: 2},
        {text: '游戏', value: 3},
      ]
    }
  },
  computed:{
    doImg(){
      return param => {
        return 'http://172.18.44.25:8888'+param;
      }
    }
  },
  filters:{
    price(param){
      return '￥'+param
    },
    sold(param){
      return '月销'+param+'笔'
    }
  },
  /*
  */
  methods:{
    doLike(){
      if(this.like==''){
        if(this.selected === 0){
          this.axios.get('/goodsImg')
          .then(res => {
            this.list = res.data.list
          })
        }else{
          this.axios.get('/goodsImg?type='+this.selected)
          .then(res => {
            this.list = res.data.list
          })
        }
      }else{
        if(this.selected === 0){
          this.axios.get('/goodsImg?like='+this.like)
          .then(res => {
            this.list = res.data.list
          })
        }else{
          this.axios.get('/goodsImg?type='+this.selected+'&like='+this.like)
          .then(res => {
            this.list = res.data.list
          })
        }
      }
    }
  },
  created(){
    this.axios.get('/goodsImg')
    .then(res => {
      this.list = res.data.list
    })
  }
}
</script>

<style scoped>
img.img{
  height: 55px;
  width: 55px;
}
</style>


